import * as React from 'react';
import { Row, Col } from 'antd';
import { LeftSideBar } from './LeftSideBar';
import LeftSideBarWithoutMenu from './LeftSideBarWithoutMenu';
import { RightSideBar } from './RightSideBar';
import './MainContent.less';
import { Brand, Adsense, FeatureBrand } from '../../services/service-proxies';
import { getServiceProxy } from '@client/services';
import BrandList from '@client/modules/machine-news/screens/HomePageScreen/components/BrandList';

interface Props {
  topBrands: Brand[];
  hasMenuItems: boolean;
  hasSideBar: boolean;
  children: any;
  hasBrandList?: boolean;
}
interface State {
  adsense: Adsense[];
  featureBrands: FeatureBrand[];
}
export class MainContent extends React.Component<Props, State> {
  state: State = {
    adsense: [],
    featureBrands: [],
  };

  componentDidMount () {
    this.getAdsense();
    this.getFeatureBrands();
  }

  getAdsense = async () => {
    try {
      const serviceProxy = getServiceProxy();
      const adsense = await serviceProxy.getAdsense(5, 'position|asc', undefined, undefined);
      this.setState({
        adsense: adsense.data,
      });
    } catch (error) {
      // tslint:disable-next-line: no-console
      console.log(error);
    }
  }

  getFeatureBrands = async () => {
    try {
      const serviceProxy = getServiceProxy();
      const featureBrands = await serviceProxy.getFeatureBrands(10, 'order|asc', undefined, undefined);
      this.setState({
        featureBrands: featureBrands.data,
      });
    } catch (error) {
      // tslint:disable-next-line: no-console
      console.log(error);
    }
  }

  render () {
    const adsPosition1 = this.state.adsense.filter((item) => item.position === 1)[0];
    const adsPosition3 = this.state.adsense.filter((item) => item.position === 3)[0];

    return (
      <main className='main-content'>
        {this.props.hasBrandList && (
          <Row className='home-page-brand-list' type='flex' justify='center'>
            <Col lg={3} md={0} sm={0} xs={0}>
              <a href={adsPosition1 && adsPosition1.hyperlink ? adsPosition1.hyperlink : '#'}>
                <img
                  className='image-promote'
                  src={adsPosition1 && adsPosition1.imageUrl ? adsPosition1.imageUrl : '/static/images/promotion.png'}
                  style={{ maxHeight: '60px' }}
                />
              </a>
            </Col>
            <Col lg={18} md={24} xs={24} sm={24}>
              <BrandList featureBrands={this.state.featureBrands} />
            </Col>
            <Col lg={3} md={0} sm={0} xs={0}>
              <a href={adsPosition3 && adsPosition3.hyperlink ? adsPosition3.hyperlink : '#'}>
                <img
                  className='image-promote'
                  src={adsPosition3 && adsPosition3.imageUrl ? adsPosition3.imageUrl : '/static/images/promotion.png'}
                  style={{ maxHeight: '60px' }}
                />
              </a>
            </Col>
          </Row>
        )}

        {this.props.hasSideBar ? (
          <Row className='padding-of-page'>
            <Col lg={3} md={24} sm={24} xs={24} >
              {this.props.hasMenuItems ? (
                <LeftSideBar topBrands={this.props.topBrands} adsense={this.state.adsense} />
              ) : (
                <LeftSideBarWithoutMenu />
              )}
            </Col>
            <Col className='main-col' lg={18} md={24} sm={24} xs={24} >
              {this.props.children}
            </Col>
            <Col lg={3} md={0} sm={0} xs={0} >
              <RightSideBar adsense={this.state.adsense} />
            </Col>
          </Row>
        ) : (
          <Row className='padding-of-page'>
            <Col lg={3} md={0} sm={0} xs={0} />
            <Col className='main-col' lg={18} md={24} sm={24} xs={24} style={{ padding: '0px 20px' }}>
              {this.props.children}
            </Col>
            <Col lg={3} md={0} sm={0} xs={0} />
          </Row>
        )}
      </main>
    );
  }
}
